@charset "UTF-8";
.sk-block {
  padding-top: 0;
}

.blog-quote.sk-block {
  padding-top: 0 !important;
}
.blog-quote .case-quote.bg-blue {
  background-color: #002ea6;
  background-image: url("/wp-content/uploads/2024/06/quote-bg-2.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40px;
}
@media screen and (max-width: 1199px) {
  .blog-quote .case-quote.bg-blue {
    padding: 20px;
  }
}
@media screen and (max-width: 1024px) {
  .blog-quote .case-quote.bg-blue {
    padding: 20px;
    background-repeat: repeat;
    background-size: contain;
  }
}
.blog-quote .case-quote.bg-blue .inner-quote::before {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-open.svg");
}
.blog-quote .case-quote.bg-blue .inner-quote::after {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-close.svg");
}
.blog-quote .case-quote.bg-blue .quote-content::before {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-symbol-open.svg");
}
.blog-quote .case-quote.bg-blue .quote-content::after {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-symbol-close.svg");
}
.blog-quote .case-quote.bg-white {
  background-color: #fff;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40px;
}
.blog-quote .case-quote.bg-white .inner-quote::before {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-open-w.svg");
}
.blog-quote .case-quote.bg-white .inner-quote::after {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-close-w.svg");
}
.blog-quote .case-quote.bg-white .quote-content::before {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-symbol-open-w.svg");
}
.blog-quote .case-quote.bg-white .quote-content::after {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/quote-symbol-close-w.svg");
}
.blog-quote .case-quote.bg-white .quote-content .author::before {
  color: #002ea6;
}
.blog-quote .case-quote .inner-quote {
  position: relative;
  min-height: 350px;
  display: flex;
  align-content: center;
  align-items: center;
  padding: 10px 60px;
}
@media screen and (max-width: 1199px) {
  .blog-quote .case-quote .inner-quote {
    padding: 35px 20px;
  }
}
.blog-quote .case-quote .inner-quote::before, .blog-quote .case-quote .inner-quote::after {
  content: "";
  background-repeat: no-repeat;
  background-size: 80% 75%;
  height: 100%;
  width: 100%;
  top: 0;
  position: absolute;
}
@media screen and (max-width: 1199px) {
  .blog-quote .case-quote .inner-quote::before, .blog-quote .case-quote .inner-quote::after {
    background-size: contain;
    width: 85%;
  }
}
.blog-quote .case-quote .inner-quote::before {
  background-position: bottom left;
  left: 0;
}
.blog-quote .case-quote .inner-quote::after {
  background-position: top right;
  right: 0;
}
.blog-quote .case-quote .inner-quote .quote-content {
  max-width: 90%;
  padding: 10px 15px;
  margin: auto;
}
@media screen and (max-width: 399px) {
  .blog-quote .case-quote .inner-quote .quote-content {
    max-width: 100%;
    padding: 0;
  }
}
.blog-quote .case-quote .inner-quote .quote-content::before, .blog-quote .case-quote .inner-quote .quote-content::after {
  content: "";
  width: 70px;
  height: 70px;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}
@media screen and (max-width: 1199px) {
  .blog-quote .case-quote .inner-quote .quote-content::before, .blog-quote .case-quote .inner-quote .quote-content::after {
    width: 50px;
    height: 50px;
  }
}
@media screen and (max-width: 399px) {
  .blog-quote .case-quote .inner-quote .quote-content::before, .blog-quote .case-quote .inner-quote .quote-content::after {
    width: 30px;
    height: 30px;
  }
}
.blog-quote .case-quote .inner-quote .quote-content::before {
  top: 0;
  left: 0;
}
.blog-quote .case-quote .inner-quote .quote-content::after {
  bottom: 0;
  right: 0;
}
.blog-quote .case-quote .inner-quote .quote-content .quote-text,
.blog-quote .case-quote .inner-quote .quote-content .quote-text p {
  font-family: "Red Hat Display", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 36px;
}
.blog-quote .case-quote .inner-quote .quote-content .author::before {
  content: "—";
  margin-right: 5px;
  font-size: 18px;
}
.blog-quote .case-quote .inner-quote .quote-content .author .person {
  font-family: "Libre Franklin", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 30px;
}
.blog-quote .case-quote .inner-quote .quote-content .author .company {
  font-family: "Libre Franklin", sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  margin-left: 5px;
}